//import liraries
import React, {useEffect, useState} from 'react';
import {ScrollView, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import {useAppSelector} from '../../../../../../hooks/useRedux';
import {IPostMessageUserTask, STATUS_TASK} from '../../../interface';
import TableCM from '../../../../../components/Table';
import {SizeDP} from '../../../../../../constants/Size';
import TextCM from '../../../../../components/Text';
import {
  IconDownload,
  IconTrash,
  IconUpload,
  IconView,
} from '../../../../../../assets/images';
import PagingCM from '../../../../../components/Paging';
import {useNavigation} from '@react-navigation/native';
import {CommonUtils, DateUtils} from '../../../../../../utils';
import Toast from 'react-native-toast-message';
import {
  IInfoFile,
  IInfoFileRelative,
} from '../../../../ticket-detail/components/TabView/components/Document/interface';
import NavigationName from '../../../../../../constants/NavigationName';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../../../hooks/apiHooks/useReviewTicketServices';

type Props = {
  dataUserTask: IPostMessageUserTask;
};
// create a component
const Document = ({dataUserTask}: Props) => {
  const pageSize = 3;
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const [currentPage, setCurrentPage] = useState(1);
  const [totalItems, setTotalItems] = useState<number>(0);
  const [dataRelative, setDataRelative] = useState<any[]>([]);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    getFileByUserTask(currentPage);
  }, [currentPage]);

  const getFileByUserTask = async (page: number) => {
    try {
      const params = {
        page: page - 1,
        size: pageSize,
        sort: 'createdDate,desc',
      };
      const dataResp = await ReviewTicketServices.getListAttachmentsUserTask(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
        params,
        accessToken,
      );
      if (dataResp) {
        if (dataResp?.data?.length === 0) {
          if (page > 1) {
            setCurrentPage(page - 1);
          } else {
            setDataRelative([]);
            setTotalItems(0);
          }
          return;
        }
        setTotalItems(dataResp?.totalRecords);
        convertDataFileToTable(dataResp?.data);
      }
    } catch (error) {}
  };

  const getListFile = async () => {
    try {
      const dataFile = await ReviewTicketServices.getListFileReview(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
        accessToken,
      );
    } catch (error) {}
  };

  const renderActionInfoFile = (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    return (
      <View style={styles.ctnAction}>
        <TouchableOpacity
          onPress={() => handleViewFile(isInfoFile, data)}
          hitSlop={SizeDP(15)}>
          <IconView width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        <TouchableOpacity hitSlop={SizeDP(15)}>
          <IconDownload width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        {!isInfoFile && dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
          <TouchableOpacity
            onPress={() => handleClearFile(data?.id?.toString())}
            hitSlop={SizeDP(15)}>
            <IconTrash width={SizeDP(16)} height={SizeDP(16)} />
          </TouchableOpacity>
        )}
      </View>
    );
  };

  const handleViewFile = async (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.info'),
        text2: t('TK.viewFile'),
      });

      const formData = new FormData();
      let keyName: string = (data as any).name;
      if (isInfoFile) {
        keyName = (data as IInfoFile).keyName;
      } else {
        keyName = (data as IInfoFileRelative).name;
      }
      let bucketName: string = data.bucketName;
      if (!data.bucketName) {
        const arr = (data as any)?.name?.split('.s3-hfx03.fptcloud.com:443/');
        keyName = arr[1];
        bucketName = arr[0].split('https://')[1];
      }
      formData.append('keyName', keyName);
      formData.append('bucketName', bucketName);
      const dataView = await ReviewTicketServices.viewFile(
        formData,
        accessToken,
      );
      if (dataView?.code == 1) {
        navigation.navigate(NavigationName.ViewFileScreen, {
          url: dataView.message,
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('CM.error'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('CM.error'),
      });
    }
  };

  const handleClearFile = async (idFile: string) => {
    try {
      const data = await ReviewTicketServices.deleteFileRelative(
        idFile,
        accessToken,
      );
      await getFileByUserTask(currentPage);
      Toast.show({
        type: 'success',
        text1: t('CM.info'),
        text2: t('TK.deleteFileSuccess'),
      });
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('TK.deleteFileErr'),
      });
    }
  };

  const convertDataFileToTable = (data: IInfoFileRelative[]) => {
    const newData = data.map((infoFile: IInfoFileRelative, index: number) => ({
      STT: String((currentPage - 1) * pageSize + 1 + index),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {infoFile?.fileName}
        </TextCM>
      ),
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile.size),
      type: infoFile?.fileName?.split('.')?.pop(),
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(false, infoFile),
    }));
    setDataRelative(newData);
  };

  const handleUploadFile = async () => {
    try {
      const file = await CommonUtils.handleGetFile();
      if (!file) return;

      const formData = new FormData();
      formData.append('uploadFile', file);
      formData.append(
        'attachmentJson',
        JSON.stringify({
          ticketId: dataTicketRedux?.id,
          userTaskId: dataUserTask._userTaskId,
        }),
      );
      const data = await ReviewTicketServices.uploadFileRelative(
        formData,
        accessToken,
      );
      if (data) {
        await getFileByUserTask(currentPage);
        Toast.show({
          type: 'success',
          text1: t('CM.info'),
          text2: t('TK.uploadFileSuccess'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('TK.uploadFileErr'),
        });
      }
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('TK.uploadFileErr'),
      });
    }
  };

  return (
    <View style={styles.ctnMain}>
      <ScrollView style={styles.ctnBody}>
        <View style={styles.ctnForm}>
          <View style={styles.viewForm}>
            <TextCM style={styles.txtTitle}>{t('RV.document')}</TextCM>
            {dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
              <TouchableOpacity onPress={handleUploadFile} hitSlop={SizeDP(15)}>
                <IconUpload width={SizeDP(16)} height={SizeDP(16)} />
              </TouchableOpacity>
            )}
          </View>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataRelative}
          />
          {totalItems > 0 && (
            <PagingCM
              collectionSize={totalItems}
              currentPage={currentPage}
              pageSize={pageSize}
              onPageChange={currentPage => setCurrentPage(currentPage)}
            />
          )}
        </View>

        {/* <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateInfo')}</TextCM>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={[]}
          />
        </View> */}
      </ScrollView>
    </View>
  );
};

//make this component available to the app
export default Document;
