import WebView from 'react-native-webview';
import {BASE_URL_FORM} from '../../../../../services/httpClient';
import {String} from '../../../../../constants';
import {useRef} from 'react';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {useAppSelector} from '../../../../../hooks/useRedux';

type Props = {
  versionForm: string;
  userTaskId: string;
  ticketId: string;
};

const EkycData = ({versionForm, userTaskId, ticketId}: Props) => {
  const webviewRef = useRef<WebView>(null);
  const {accessToken} = useAppSelector(state => state.common);
  const ReviewServices = useReviewServices();

  const getFormDataLastest = async () => {
    const body = {
      userTaskId,
      ticketId,
    };
    const dataFrom = await ReviewServices.getFormVersionByTicketAndUserTaskId(
      body,
      accessToken,
    );

    return dataFrom;
  };

  const onMessage = async (event: any) => {
    const res = JSON.parse(event.nativeEvent.data);
    if (res.result === 'e-from') {
      patchValue();
    }
  };

  const patchValue = async () => {
    const data = await getFormDataLastest();
    if (data) {
      const dataFrom = JSON.parse(data?.formData);

      const formData = JSON.stringify({
        type: 'postFormEdit',
        isReadonly: true,
        data: dataFrom,
      });
      const scriptJSX = `window.postMessage('${formData
        .replace(/\\/g, '\\\\')
        .replace(/'/g, "\\'")}', '*');`;
      setTimeout(() => {
        webviewRef?.current?.injectJavaScript(scriptJSX);
      }, 800);
    }
  };
  return (
    <>
      <WebView
        style={{height: 500}}
        startInLoadingState
        scrollEnabled={true}
        nestedScrollEnabled={true}
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}
        injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
        allowsInlineMediaPlayback={true}
        mediaPlaybackRequiresUserAction={false}
        ref={webviewRef}
        onMessage={onMessage}
        source={{uri: BASE_URL_FORM + versionForm}}
      />
    </>
  );
};

export default EkycData;
